.blue_btn {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 10px;
  width: 150px;
  height: 40px;
  background-color: #00A4E5;
  transition: 0.3s ease;
  cursor: pointer;
}
.blue_btn a {
  color: #ffffff;
  font-size: 13px;
  line-height: 15px;
  letter-spacing: 0.5px;
}
.blue_btn:hover {
  opacity: 0.8;
}